import Vue from 'vue'
import Vuex from 'vuex'
import {HTTP_CLIENT} from "@/http/axios-config";

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        audio: {
            isPlaying: false,
            volume: 0.5,
            currentTime: 0,
            duration: 0
        },
        artists: [],
        songs: {},
        playingSongIdx: -1
    },
    getters: {
        getArtists: (state) => {
            return state.artists;
        },
        getCurrentArtistId: (state, getters) => {
            return getters.getCurrentSong != null ? getters.getCurrentSong.artist.id : null;
        },
        getCurrentAlbumId: (state, getters) => {
            return getters.getCurrentSong != null ? getters.getCurrentSong.album.id : null;
        },
        getCurrentArtistName: (state, getters) => {
            return getters.getCurrentSong != null ? getters.getCurrentSong.artist.name : null;
        },
        getCurrentAlbumName: (state, getters) => {
            return getters.getCurrentSong != null ? getters.getCurrentSong.album.name : null;
        },
        getCurrentSong: (state) => {
            if (state.playingSongIdx < 0) {
                return null;
            }
            return state.songs[state.playingSongIdx];
        },
        getCurrentSongName: (state, getters) => {
            return getters.getCurrentSong != null ? getters.getCurrentSong.name : '';
        },
        getArtWorkBaseUrl: (state) => {
            return HTTP_CLIENT.defaults.baseURL + "/artwork/open/";
        },
        getCurrentSongArtworkSrc: (state, getters) => {
            if (getters.getCurrentSong) {
                return getters.getArtWorkBaseUrl + '?songId=' + getters.getCurrentSong.id;
            }
            return null;
        },
        hasNextSong: (state) => {
            return state.playingSongIdx + 1 < state.songs.length;
        },
        hasPrevSong: (state) => {
            return state.playingSongIdx - 1 >= 0;
        },
        getCurrentSongDurationInMinutesAndSeconds: (state) => {
            let seconds = state.audio.duration;
            return Math.floor(seconds / 60) + ':' + ('0' + Math.floor(seconds % 60)).slice(-2);
        },
        audioIsPlaying: (state) => {
            return state.audio.isPlaying;
        }
    },
    mutations: {
        setAudioCurrentTime: (state, payload) => {
            state.audio.currentTime = payload;
        },
        setAudioProperties: (state, payload) => {
            state.audio.paused = payload.paused;
            state.audio.volume = payload.volume;
            state.audio.currentTime = payload.currentTime;
            state.audio.duration = payload.duration;
        },
        setArtists: (state, payload) => {
            state.artists = payload;
        },
        setSongs: (state, payload) => {
            state.songs = payload.songs;
            state.playingSongIdx = payload.playingSongIdx;
        },
        setCurrentSongToPrev: (state) => {
            state.playingSongIdx--;
        },
        setCurrentSongToNext: (state) => {
            state.playingSongIdx++;
        },
        setAudioIsPlaying: (state, payload) => {
            state.audio.isPlaying = payload;
        },
        setAudioVolume: (state, payload) => {
            state.audio.volume = payload;
        }
    }
});