<template>
  <div id="mini-player">
    <b-navbar fixed="bottom" type="light" variant="light">
      <b-collapse id="nav-collapse" is-nav>

        <b-navbar-nav>

          <b-nav-form>
            <b-img style="max-width:50px;max-height:50px" center v-if="this.$store.getters.getCurrentSongArtworkSrc" v-bind:src="this.$store.getters.getCurrentSongArtworkSrc" alt=""></b-img>
            <b-button v-if="!this.$store.getters.getCurrentSongArtworkSrc" disabled variant="link">
              <b-icon icon="music-note-beamed"/>
            </b-button>
          </b-nav-form>

          <div v-if="this.$store.getters.getCurrentSong">
            <b-nav-item style="margin-left: 20px">
              <b-link v-bind:to="{name: 'Player', params: {songId: this.$store.getters.getCurrentSong.id}}" style="color: inherit">
                {{this.$store.getters.getCurrentSong.name}}
              </b-link>
            </b-nav-item>
          </div>

        </b-navbar-nav>

        <b-navbar-nav class="ml-auto">

          <b-nav-form>

            <b-input-group>

              <b-input-group-prepend>

<!--                <b-input-group-append>-->
<!--                  <b-button variant="link" style="color: inherit;">-->
<!--                    <b-icon id="volume-popover" icon="volume-down"></b-icon>-->
<!--                  </b-button>-->
<!--                  <b-popover target="volume-popover" triggers="focus">-->
<!--                    <b-row align="left">-->
<!--                      <b-col cols="2" align="right">-->
<!--                        <b-icon icon="volume-off" font-scale="1.5"></b-icon>-->
<!--                      </b-col>-->
<!--                      <b-col>-->
<!--                        <b-form-input type="range" min="0" step="0.01" max="1.0"/>-->
<!--                      </b-col>-->
<!--                      <b-col cols="2" align="left">-->
<!--                        <b-icon icon="volume-up" font-scale="1.5"></b-icon>-->
<!--                      </b-col>-->
<!--                    </b-row>-->
<!--                  </b-popover>-->
<!--                </b-input-group-append>-->

                <b-input-group-append>
                  <b-button v-if="!this.$store.state.audio.isPlaying" @click="play()" variant="link" style="color: inherit">
                    <b-icon icon="play" font-scale="2"></b-icon>
                  </b-button>
                  <b-button v-if="this.$store.state.audio.isPlaying" @click="pause()" variant="link" style="color: inherit">
                    <b-icon icon="pause" font-scale="2"></b-icon>
                  </b-button>
                </b-input-group-append>

                <b-input-group-append>
                  <b-button variant="link" style="color: inherit" @click="playNext()">
                    <b-icon icon="skip-end" font-scale="2"></b-icon>
                  </b-button>
                </b-input-group-append>

              </b-input-group-prepend>
            </b-input-group>
          </b-nav-form>
        </b-navbar-nav>
      </b-collapse>

    </b-navbar>
  </div>
</template>

<script>

import {eventBus} from "@/main";
import {BIcon, BIconSkipStart, BIconPlay, BIconPause, BIconSkipEnd, BIconShuffle, BIconArrowRepeat, BIconMusicNoteBeamed, BIconVolumeUp, BIconVolumeDown, BIconVolumeOff} from 'bootstrap-vue';

export default {
  name: 'MiniPlayer',
  components: {
    BIcon, BIconSkipEnd, BIconPlay, BIconPause, BIconSkipStart, BIconShuffle, BIconArrowRepeat, BIconMusicNoteBeamed, BIconVolumeUp, BIconVolumeDown, BIconVolumeOff
  },
  methods: {
    play() {
      eventBus.$emit('player-play');
    },
    playNext() {
      if (this.$store.getters.hasNextSong) {
        this.$store.commit('setCurrentSongToNext');
        eventBus.$emit('player-load');
      }
    },
    pause() {
      eventBus.$emit('player-set-on-pause');
    }
  }
}

</script>