<template>
  <div>

    <div class="text-left">
      <b-link to="/library/artists">
        <b-icon style="margin-top: 5px" icon="chevron-left"></b-icon>
        Artists
      </b-link>
    </div>

    <b-list-group class="pqd-list-group">

      <b-list-group-item class="pqd-list-group-item">
        <h3>{{this.artistName}}</h3>
      </b-list-group-item>

      <div v-if="artistAlbums.albums && artistAlbums.albums.length">
        <b-list-group-item
            class="pqd-list-group-item"
            button v-for="album of artistAlbums.albums" :key="album.name" @click="onAlbumSelected(album.id)">
          {{album.name}}
        </b-list-group-item>
      </div>
<!--      <div v-else>-->
<!--        <b-list-group-item class="pqd-list-group-item" button @click="onAlbumSelected('noalbum')">-->
<!--          Album unknown-->
<!--        </b-list-group-item>-->
<!--      </div>-->
    </b-list-group>
  </div>
</template>

<script>

import {BIcon, BIconChevronLeft} from 'bootstrap-vue'
import {HTTP_CLIENT} from "@/http/axios-config"

export default {
  name: 'Albums',
  components: {
    BIcon,
    BIconChevronLeft
  },
  data() {
    return {
      artistAlbums: {},
      artistName: ''
    }
  },
  methods: {
    onAlbumSelected(albumId) {
      this.$router.push({name: 'Songs', params: {albumId: albumId}});
    }
  },
  created() {
    let artistId = this.$route.params.artistId;
    HTTP_CLIENT.get('/library/artists/' + artistId + '/albums').then(response => {
      this.artistAlbums = response.data;
      this.artistName = this.artistAlbums.artist.name
    })
  }
}
</script>