<template>
  <div>

    <b-row>
      <b-col>
        <div class="text-left">
          <b-link to="/library">
            <b-icon style="margin-top: 5px" icon="chevron-left"/>
            Library
          </b-link>
        </div>
      </b-col>
    </b-row>

    <b-row style="padding-top: 20px;padding-bottom: 10px">
      <b-col>
        <div class="text-left">
          <h3>Artists</h3>
        </div>
      </b-col>
    </b-row>

    <b-row>
      <b-col>
        <b-input-group>
          <b-input-group-prepend>
            <b-input-group-text>
              <b-icon icon="search"/>
            </b-input-group-text>
          </b-input-group-prepend>
          <b-form-input type="search"
                        v-model="searchFilter"
                        @input="filterArtists()"
                        placeholder="Find in Artists">
          </b-form-input>
        </b-input-group>
      </b-col>
    </b-row>

    <b-row>
      <b-col>
        <b-list-group v-if="artists.length > 0" class="pqd-list-group">
          <b-list-group-item class="pqd-list-group-item"/>

          <b-list-group-item class="pqd-list-group-item"
                             button v-for="artist of artists" :key="artist.name" @click="onArtistSelected(artist.id)">
            <!--        <b-img v-bind:blank="true" width="25" height="25" blank-color="#D0D0D0" rounded="circle" alt="HEX color image"></b-img>-->
            {{artist.name}}
          </b-list-group-item>
        </b-list-group>
        <div v-else style="padding-top: 20%">
          <h2>No results</h2>
        </div>
      </b-col>
    </b-row>
  </div>
</template>

<script>

import {BIcon, BIconChevronLeft, BIconSearch} from 'bootstrap-vue'
import {HTTP_CLIENT} from "@/http/axios-config"

export default {
  name: 'Artists',
  components: {
    BIcon, BIconChevronLeft, BIconSearch
  },
  data() {
    return {
      searchFilter: '',
      artists: []
    }
  },
  methods: {
    onArtistSelected(artistId) {
      this.$router.push({name: 'Albums', params: {artistId: artistId}});
    },
    filterArtists() {
      if (this.searchFilter === '') {
        this.artists = this.$store.getters.getArtists;
      } else {
        let filter = this.searchFilter.toLowerCase();
        this.artists = this.$store.getters.getArtists.filter(artist => artist.name.toLowerCase().startsWith(filter));
      }
    }
  },
  created() {
    if (this.$store.getters.getArtists.length === 0) {
      HTTP_CLIENT.get('/library/artists/').then(response => {
        this.$store.commit('setArtists', response.data.artists);
        this.artists = this.$store.getters.getArtists;
      }).catch(function(error) {
        alert(error)
      });
    } else {
      this.artists = this.$store.getters.getArtists;
    }
  }
}
</script>